<template>
  <div>
    <div class="home-nav">
      <NavBar></NavBar>
    </div>
    <div class="tab-controller" ref="tab" v-show="tabControllerShow">
      <Tab></Tab>
    </div>
    <ScrollComponent @doTabFixed="doTabFixed" @pullingUp="doPullingUp" @doCanBackTop="doCanBackTop" ref="scroll"
                     class="scroll"
                     v-if="banner.length>0 && recommend.length>0&&goods.pop.list.length>0">
      <div class="home-slider">
        <WrapperComponent :list="banner"></WrapperComponent>
      </div>

      <div class="home-recommend">
        <RecommendComponent :list="recommend"></RecommendComponent>
      </div>
      <div class="home-tab" ref="tab">
        <Tab></Tab>
      </div>
      <ListComponent :list="goods.pop.list" v-on:doLoadDown="doLoadDown"></ListComponent>
    </ScrollComponent>
    <BackTopComponent v-show="backTopShow" @click.native="doBackTop"></BackTopComponent>
  </div>

</template>

<script>
import NavBar from '@/components/content/NavBar.vue'
import homeService from '@/services/homeService.js'
import WrapperComponent from '@/components/common/wrapper/WrapperComponent.vue'
import RecommendComponent from '@/components/common/recommend/RecommendComponent.vue'
import Tab from "@/views/home/Tab";
import ListComponent from "@/components/common/list/ListComponent";
import ScrollComponent from "@/components/common/scroll/ScrollComponent";
import BackTopComponent from "@/components/common/backTop/BackTopComponent";
import {debounce} from "@/common/until";
import {backTop} from "@/common/mixin";

export default {
  name: "Home",
  data() {
    return {
      banner: [],
      recommend: [],
      goods: {
        'pop': {page: 0, list: []},
        'news': {page: 0, list: []},
        'sell': {page: 0, list: []}
      },
      // backTopShow: false,
      currentPage: 1,
      tabOffsetTop: 0,
      tabControllerShow: 0,
      debounce:null
    }
  },
  components: {
    NavBar,
    WrapperComponent,
    RecommendComponent,
    Tab,
    ListComponent,
    ScrollComponent,
    BackTopComponent
  },
  created() {
    this.banner = homeService.getBanner()
    this.recommend = homeService.getRecommend()
    this.goods.pop.list = homeService.getTabs(this.currentPage)
    const bbb = this
    // es5写法
    // this.debounce = debounce(function (...args){
    //   console.log(args)
    //   bbb.$refs.scroll.refresh()},50,bbb)
    // es6写法
    this.debounce = debounce(function (...args){
      console.log(args)
      bbb.$refs.scroll.refresh()},50,bbb)

  },
  mounted() {
    this.tabOffsetTop = this.$refs.tab.offsetTop - 44
  },
  destroyed() {
    console.log('home destroy');
  },
  activated() {
    if (this.tabControllerShow) {
      this.$refs.scroll.scroll.scrollTo(0, this.tabControllerShow)
      this.$refs.scroll.refresh()
    }
  },
  mixins:[backTop],
  methods: {
    doLoadDown() {
      this.debounce('aaa')
    },
    // doBackTop() {
    //   this.$refs.scroll.scroll.scrollTo(0, 0, 300)
    // },
    // doCanBackTop(show) {
    //   this.backTopShow = show
    // },
    doPullingUp() {
      const page = this.currentPage = this.currentPage + 1
      // const data = this.goods.pop.list
      // this.goods.pop.list = data.concat(homeService.getTabs(page))
      this.goods.pop.list.push(...homeService.getTabs(page))
      // 重新可以下拉更新
      this.$refs.scroll.scroll.finishPullUp()


    },
    doTabFixed(y) {
      if (this.tabOffsetTop < Math.abs(y)) {
        this.tabControllerShow = y
      } else {
        this.tabControllerShow = 0
      }
    }
  },
  // computed: {
  //   imgStyle() {
  //     return {
  //       width: '32px',
  //       height: '32px',
  //       display: 'block',
  //     }
  //   }
  // }

}
</script>

